.Section {
    padding-right: 50px;
    padding-left: 50px;
}

.title {
    margin-top: 30px;
    margin-bottom: 30px;
    padding-left: 5px;
    font-size: 30px;
    font-weight: 600;
    text-align: center;
}

.list {
    list-style: disc;
    color: #0d57aa;
}

.listItem:not(:last-child) {
    margin-bottom: 8px;
}

.link {
    font-size: 20px;
    color: #0d57aa;
    transition: color 0.2s ease-in-out, border-bottom 0.2s ease-in-out;
}

.link:hover,
.link:focus {
    color: #1c85ff;
    border-bottom: 2px solid #1c85ff;
}